import * as React from "react";
import { Component } from 'react';
import { Route } from 'react-router-dom'
import ReactDOM from 'react-dom';

export default class Content extends Component {
  render() {
    let debug_data = [];
    debug_data.push( `window.screen.availHeight: ${window.screen.availHeight}`);
    debug_data.push( `window.screen.availWidth: ${window.screen.availWidth}`);
    debug_data.push( `window.screen.height: ${window.screen.height}`);
    debug_data.push( `window.screen.width: ${window.screen.width}`);
    debug_data.push( `$(window).height(): ${window.$(window).height()}`);
    debug_data.push( `$(window).width(): ${window.$(window).width()}`);

    var routes = this.props.routes || [];
    return (
      <div className={`content ${this.props.moreClassName || ""}`}>
        {routes.map((route) => (
          React.createElement(route.component, { key: route.id, id: route.id })
        ))}
        <div style={{ "white-space": "pre" }}>
          {debug_data.join("\n")}
        </div>
      </div>
    );
  }
}




